@import url('https://fonts.googleapis.com/css?family=Ubuntu&display=swap');

html,
body {
  height: 100%;
  overflow: auto;
  font-family: 'Ubuntu', sans-serif;
}

table {
  tr,
  td {
    padding: 3px 5px;
  }
}

.container-fluid {
  width: 95%;
  margin: 0 auto;
  max-width: initial;
  > .row {
    margin: 0 auto;
    > .col {
      padding: 1rem;
    }
  }
}
