@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
.users-container{margin-top:50px}.chart{width:80%;height:500px;margin:50px auto 0 auto}@media screen and (max-width: 992px){.chart{width:100%;height:400px}}@media screen and (max-width: 568px){.users-container{margin-top:0}}

